import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Navigation from "./Navigation.js";
import "./Pay.css";
import backButton from "./backButton.png";

function Pay() {
  const [click, setClick] = useState(false);
  const [sum, setSum] = useState(0);
  const navigate = useNavigate();

  const [activeBalance, setActiveBalance] = useState(0);

  async function getBalance() {
    let Token;

    let TokenR;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const response = await fetch("https://alvainfo.kz/api/wallet/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log(mistake);
        throw new Error("401 or badrequest");
      } else {
        const result = await response.json();
        setActiveBalance(result.active_balance);
        console.log(result, activeBalance);
      }
      return response.ok;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  useEffect(() => {
    getBalance();
  }, []);

  return (
    <>
      <div className="pay">
        <header>
          <h1></h1>
          <div>
            <img src={backButton} onClick={() => navigate(-1)} />
          </div>
        </header>
        <p>   : {activeBalance} </p>
        <form>
          <label>
            <input
              className="default"
              type="text"
              placeholder=" "
              onChange={(event) => setSum(event.target.value)}
            />
          </label>
          <div className="button" onClick={() => setClick(true)}>
            <p></p>
          </div>
          {click === true && (
            <>
              <p className="requestSum">
                   : {sum} .
                <br />
                   .
              </p>
            </>
          )}
        </form>
      </div>

      <Navigation active="Profile" />
    </>
  );
}

export default Pay;
