import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Navigation from "./Navigation.js";
import "./UserEdit.css";
import backButton from "./backButton.png";

function UserEdit() {
  const [click, setClick] = useState(false);
  const [sum, setSum] = useState(0);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [oldFirstName, setOldFirstName] = useState("");
  const [oldLastName, setOldLastName] = useState("");
  const navigate = useNavigate();

  function parseJwt(token) {
    let base64Url = token.split(".")[1];
    let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
    let jsonPayload = decodeURIComponent(
      atob(base64)
        .split("")
        .map(function (c) {
          return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
        })
        .join("")
    );
    return JSON.parse(jsonPayload);
  }

  function getOldName() {
    let Token;

    let TokenR;
    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");

      if (
        parseJwt(localStorage.getItem("tokenAccess")).user.first_name +
          " " +
          parseJwt(localStorage.getItem("tokenAccess")).user.last_name !==
        " "
      ) {
        if (localStorage.getItem("fullname")) {
          console.log(localStorage.getItem("fullname"));
          setOldFirstName(localStorage.getItem("fullname").split(" ")[0]);
          setOldLastName(localStorage.getItem("fullname").split(" ")[1]);
        } else {
          setOldFirstName(
            parseJwt(localStorage.getItem("tokenAccess")).user.first_name
          );
          setOldLastName(
            parseJwt(localStorage.getItem("tokenAccess")).user.last_name
          );
        }
      }

      console.log(oldFirstName, oldLastName);
    }
  }

  async function userEdit(firstName, lastName) {
    let Token;

    let TokenR;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
      localStorage.setItem("fullname", firstName + " " + lastName);
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const response = await fetch(
        "https://alvainfo.kz/api/user/user_profile_edit/",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: `Bearer ${Token}`,
          },
          body: JSON.stringify({
            first_name: firstName,
            last_name: lastName,
          }),
        }
      );
      console.log(response);
      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log(mistake);
        throw new Error("401 or badrequest");
      } else {
        if (localStorage) {
          localStorage.setItem("fullname", firstName + " " + lastName);
        }
        setClick(true);
        navigate("/profile");
      }
      return response.ok;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  useEffect(() => {
    getOldName();
  }, []);

  return (
    <>
      <div className="userEdit">
        <header>
          <h1> </h1>
          <div>
            <img src={backButton} onClick={() => navigate(-1)} />
          </div>
        </header>
        <form>
          <label>
            <input
              className="default"
              type="text"
              placeholder={oldFirstName ? oldFirstName : ""}
              onChange={(event) => setFirstName(event.target.value)}
            />
          </label>
          <label>
            <input
              className="default"
              type="text"
              placeholder={oldLastName ? oldLastName : ""}
              onChange={(event) => setLastName(event.target.value)}
            />
          </label>
          <div
            className="button"
            onClick={() => {
              userEdit(firstName, lastName);
            }}
          >
            <p></p>
          </div>
          {click === true && (
            <>
              <p className="requestSum"> .</p>
            </>
          )}
        </form>
      </div>

      <Navigation active="Profile" />
    </>
  );
}

export default UserEdit;
