import React from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import Navigation from "./Navigation.js";
import "./Profile.css";
import wallet from "./wallet.png";

import { useState, useEffect } from "react";

function Profile() {
  const [activeBalance, setActiveBalance] = useState(0);
  const [fullname, setFullname] = useState("");
  const navigate = useNavigate();

  async function getBalance() {
    let Token;
    let TokenR;
    let fullname;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");

      console.log(" ", localStorage.getItem("sessionID"));

      console.log(parseJwt(localStorage.getItem("tokenAccess")));

      setFullname(
        parseJwt(localStorage.getItem("tokenAccess")).user.first_name +
          " " +
          parseJwt(localStorage.getItem("tokenAccess")).user.last_name
      );

      if (
        parseJwt(localStorage.getItem("tokenAccess")).user.first_name +
          " " +
          parseJwt(localStorage.getItem("tokenAccess")).user.last_name ===
        " "
      ) {
        if (localStorage.getItem("fullname")) {
          setFullname(localStorage.getItem("fullname"));
        } else {
          setFullname(
            parseJwt(localStorage.getItem("tokenAccess")).user.phone_number
          );
        }
      }
      if (
        localStorage.getItem("fullname") &&
        localStorage.getItem("fullname") !==
          parseJwt(localStorage.getItem("tokenAccess")).user.first_name +
            " " +
            parseJwt(localStorage.getItem("tokenAccess")).user.last_name
      ) {
        setFullname(localStorage.getItem("fullname"));
      }
    }

    function parseJwt(token) {
      let base64Url = token.split(".")[1];
      let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      let jsonPayload = decodeURIComponent(
        atob(base64)
          .split("")
          .map(function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join("")
      );
      return JSON.parse(jsonPayload);
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;

      fullname = parseJwt(tokenParse.access);
    }

    try {
      const response = await fetch("https://alvainfo.kz/api/wallet/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log(mistake);
        throw new Error("401 or badrequest");
      } else {
        const result = await response.json();
        setActiveBalance(result.active_balance);
        console.log(result, activeBalance);
      }
      return response.ok;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  useEffect(() => {
    getBalance();
  }, []);

  return (
    <div className="profile">
      <h1></h1>
      <div className="user">
        <p>{fullname}</p>
      </div>
      <Link to={`/pay`}>
        <div className="wallet">
          <img src={wallet} width="30px" />
          <div className="wrapper">
            <p>
              <b></b>
            </p>
            <p>{activeBalance} </p>
          </div>
        </div>
      </Link>

      <Link to={`/useredit`}>
        <div className="button blue">
          <p> </p>
        </div>
      </Link>
      <Link to={`/passwordchange`}>
        <div className="button blue">
          <p> </p>
        </div>
      </Link>
      <Link to={`/`}>
        <div className="button">
          <p>  </p>
        </div>
      </Link>
      <Link to={`/deleteaccount`}>
        <div className="button">
          <p> </p>
        </div>
      </Link>
      <Link to={`/privacypolicy`}>
        <p className="privacyPolicy">Privacy Policy</p>
      </Link>
      <Navigation active="Profile" />
    </div>
  );
}

export default Profile;
