import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./signIn+signUp.css";
import submitIcon from "./img/submit.svg";
import alvaLogo from "./img/alva_logo.svg";

export default function SignIn() {
  const navigate = useNavigate();
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [authMistake, setAuthMistake] = useState(false);
  const [badRequest, setBadRequest] = useState("");

  function onSubmitHandler(e) {
    e.preventDefault();
    signIn(login, password)
      .then((tokensObject) => {
        console.log("aaa", tokensObject);

        if (tokensObject === undefined || tokensObject === "tokensObject") {
          setBadRequest("   ");
          return;
        }
        if (localStorage) {
          localStorage.setItem("tokenAccess", tokensObject.access);
          localStorage.setItem("tokenRefresh", tokensObject.refresh);
        }

        // eslint-disable-next-line no-undef
        if (window.Mobile) {
          // eslint-disable-next-line no-undef
          window.Mobile.consumeToken(JSON.stringify(tokensObject));
        }
        if (window["webkit"]) {
          if (window["webkit"].messageHandlers) {
            if (window["webkit"].messageHandlers.callbackHandler) {
              window["webkit"].messageHandlers.callbackHandler.postMessage(
                JSON.stringify(tokensObject)
              );
            }
          }
        }
        navigate("/map");
      })
      .catch((error) => console.log(error));
  }

  async function signIn(phoneNumber, password) {
    try {
      const signInResponse = await fetch(
        "https://alvainfo.kz/api/auth/token/",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify({
            phone_number: phoneNumber,
            password: password,
            auth_type: "phone_number",
          }),
        }
      );

      const data = await signInResponse.json();
      if (data.server_error) {
        setBadRequest(data.exception_data);
        setAuthMistake(true);
        throw new Error("Error");
      }
      if (signInResponse.status === 401 || !signInResponse.ok) {
        console.log("401 or badrequest");
        setBadRequest(data.detail);
        if (data.detail === undefined) {
          setBadRequest("  ");
        }
        setAuthMistake(true);
        throw new Error("401 or badrequest");
      } else {
        setAuthMistake(false);
      }
      console.log("hi", data);
      return data;
    } catch (error) {
      console.log(error);
      return;
    }
  }

  let inputClass = authMistake ? "mistake" : "default";

  return (
    <div className="signIn">
      <div className="logo">
        <img src={alvaLogo}></img>
      </div>

      <p className="badrequest">{badRequest}</p>
      <form onSubmit={onSubmitHandler}>
        <label>
          <input
            className={inputClass}
            type="text"
            placeholder=" "
            onChange={(event) => setLogin(event.target.value)}
          />
          <input
            className={inputClass}
            type="password"
            placeholder=""
            onChange={(event) => setPassword(event.target.value)}
          />
        </label>

        <input type="submit" value="" className="button blue" />

        <Link to={`/signup`}>
          <p> </p>
        </Link>
      </form>
    </div>
  );
}
