import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Navigation from "./Navigation.js";
import "./PasswordChange.css";
import backButton from "./backButton.png";

function PasswordChange() {
  const [response, setResponse] = useState(false);
  const [mistake, setMistake] = useState(false);
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newPasswordConfirm, setNewPasswordConfirm] = useState("");
  const navigate = useNavigate();

  async function passwordChange(oldPassword, newPassword, newPasswordConfirm) {
    let Token;

    let TokenR;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const response = await fetch(
        "https://alvainfo.kz/api/user/user_password_edit/",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: `Bearer ${Token}`,
          },
          body: JSON.stringify({
            old_password: oldPassword,
            password: newPassword,
            password2: newPasswordConfirm,
          }),
        }
      );
      console.log(response);
      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log(mistake);
        setMistake(true);
        setResponse(false);
        throw new Error("401 or badrequest");
      } else {
        setResponse(true);
        navigate("/profile");
      }
      return response.ok;
    } catch (error) {
      setMistake(true);
      setResponse(false);
      console.log(error);
      return null;
    }
  }

  return (
    <>
      <div className="passwordChange">
        <header>
          <h1> </h1>
          <div>
            <img src={backButton} onClick={() => navigate(-1)} />
          </div>
        </header>
        <form>
          <label>
            <input
              className="default"
              type="password"
              placeholder=" "
              onChange={(event) => setOldPassword(event.target.value)}
            />
          </label>
          <label>
            <input
              className="default"
              type="password"
              placeholder=" "
              onChange={(event) => setNewPassword(event.target.value)}
            />
          </label>
          <label>
            <input
              className="default"
              type="password"
              placeholder="  "
              onChange={(event) => setNewPasswordConfirm(event.target.value)}
            />
          </label>
          <div
            className="button"
            onClick={() => {
              passwordChange(oldPassword, newPassword, newPasswordConfirm);
            }}
          >
            <p></p>
          </div>
          {response === true && (
            <>
              <p className="requestSum">  .</p>
            </>
          )}

          {mistake === true && (
            <>
              <p className="mistake"> .</p>
            </>
          )}
        </form>
      </div>

      <Navigation active="Profile" />
    </>
  );
}

export default PasswordChange;
