import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import backButton from "./backButton.png";
import Navigation from "./Navigation.js";
import "./StationForMap.css";

import GBT from "./img/GBTDC.png";
import CCS from "./img/CCSComboForMapFilter.svg";

function StationForMap({ id, mode }) {
  const [stationID, setStationID] = useState(0);
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [totalPower, setTotalPower] = useState("");
  const [description, setDescription] = useState("");
  const [guns, setGuns] = useState([]);
  const [activeGun, setActiveGun] = useState(Number);

  const idFromParams = useParams().id;

  if (mode === "forList") {
    id = idFromParams;
  }

  function changeGun(id) {
    setActiveGun(id);
  }

  async function stationGet() {
    let Token;
    let TokenR;
    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const stationList = await fetch(`https://alvainfo.kz/api/station/${id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      let infoaboutstation = await stationList.json();

      if (infoaboutstation.server_error) {
        async function refresh() {
          const refreshResponse = await fetch(
            "https://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      console.log(infoaboutstation);
      setStationID(infoaboutstation.id);
      setName(infoaboutstation.name);
      setAddress(infoaboutstation.address);
      setDescription(infoaboutstation.description);
      setGuns(infoaboutstation.guns);
      console.log(infoaboutstation.guns);
    } catch (e) {
      console.log("!");
    }
  }

  function identityStatus(status) {
    if (status === "Available") {
      return "";
    } else if (status === "Unavailable") {
      return "";
    } else {
      return "";
    }
  }

  useEffect(() => {
    stationGet();
  }, []);

  const navigate = useNavigate();

  return (
    <div className="station">
      <h1>{name}</h1>
      {mode === "forList" && (
        <div className="buttons">
          <img src={backButton} onClick={() => navigate(-1)} />
        </div>
      )}
      <p className="stationAddress">{address}</p>
      {description.length > 0 && (
        <p className="stationAddress">{description}</p>
      )}
      {guns.map((gun) => {
        let type;
        let classType;
        if (gun.type.name === "CCS2") {
          type = CCS;
          classType = "stationForMapImage";
        } else {
          type = GBT;
          classType = "stationForMapImage2";
        }
        return (
          <div
            className={
              activeGun === gun.id
                ? "stationForMapRowActive"
                : "stationForMapRow"
            }
            onClick={() => changeGun(gun.id)}
          >
            <div className="stationForMapRowInner">
              <div className="stationForMapRowInnerLeft">
                <img src={type} className={classType} />
                <p>{gun.charging_power} kWh</p>
                <p>
                  <b>&nbsp;{gun.name}</b>
                </p>
              </div>
              <p>{gun.tariff.cost}   </p>
            </div>
            {description.includes("ALVA") === true && (
              <div className={"stationForMapRowStatus " + gun.status}>
                <p>{identityStatus(gun.status)}</p>
              </div>
            )}
          </div>
        );
      })}
      {description.includes("ALVA") === true && (
        <Link to={activeGun ? `/session/${stationID}/${activeGun}` : ""}>
          <div className={activeGun ? "button" : "button default"}>
            {activeGun ? <p></p> : <p> </p>}
          </div>
        </Link>
      )}
      {mode === "forList" && (
        <Link to={`/map/${stationID}`}>
          <div className="button">
            <p>  </p>
          </div>
        </Link>
      )}
      {mode === "forList" && <Navigation active="List" />}
    </div>
  );
}

export default StationForMap;
