import React from "react";
import { useState, useEffect } from "react";
import "./MapFilter.css";

function MapFilter({ handler }) {
  const [onlyGBT, setOnlyGBT] = useState(true);
  const [onlyCCS, setOnlyCCS] = useState(true);
  const [onlyAvailable, setOnlyAvailable] = useState(false);

  return (
    <div className="MapFilter">
      <h1></h1>
      <p>  </p>
      <div className="row">
        <p>  </p>
        <div className="container">
          <div className="connector GBT">
            <input
              type="checkbox"
              id="checkbox-1"
              defaultChecked="true"
              onChange={() => setOnlyGBT(!onlyGBT)}
            />
            <label htmlFor="checkbox-1"></label>
          </div>
          <div className="connector CCS">
            <input
              type="checkbox"
              id="checkbox-2"
              defaultChecked="false"
              onChange={() => setOnlyCCS(!onlyCCS)}
            />
            <label htmlFor="checkbox-2"></label>
          </div>
        </div>
      </div>
      <div className="row">
        <p> </p>
        <div className="available">
          <input
            type="checkbox"
            id="switch"
            onChange={() => setOnlyAvailable(!onlyAvailable)}
          />
          <label htmlFor="switch"></label>
        </div>
      </div>
      <div
        className="button"
        onClick={() => handler({ onlyGBT, onlyCCS, onlyAvailable })}
      >
        <p></p>
      </div>
    </div>
  );
}

export default MapFilter;
