import React from "react";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./Navigation.css";
import mapIcon from "./img/navigation/map.svg";
import listIcon from "./img/navigation/list.svg";
import profileIcon from "./img/navigation/profile.svg";
import mapIconActive from "./img/navigation/mapActive.svg";
import listIconActive from "./img/navigation/listActive.svg";
import profileIconActive from "./img/navigation/profileActive.svg";

function Navigation(props) {
  const [activeSession, setActiveSession] = useState("");
  let map = [mapIcon, "default"];
  let list = [listIcon, "default"];
  let profile = [profileIcon, "default"];

  if (props.active === "Map") {
    map[0] = mapIconActive;
    map[1] = "active";
  }

  if (props.active === "List") {
    list[0] = listIconActive;
    list[1] = "active";
  }

  if (props.active === "Profile") {
    profile[0] = profileIconActive;
    profile[1] = "active";
  }

  useEffect(() => {
    if (localStorage) {
      if (
        localStorage.getItem("sessionID") &&
        localStorage.getItem("sessionID") !== "null"
      ) {
        console.log("", localStorage.getItem("sessionID"));
        setActiveSession(localStorage.getItem("sessionID"));
      }
    }

    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      let IDID = window.Mobile.getSessionId();
      let IDparse = JSON.parse(IDID);
      if (IDparse.chargeOrderID) {
        setActiveSession(IDID.chargeOrderID);
      }
    }
  }, []);

  return (
    <>
      {activeSession && !props.mode ? (
        <Link to={`/session/${activeSession}`}>
          <div className="activeSession">
            <div className="buttonSession"> </div>
          </div>
        </Link>
      ) : (
        <></>
      )}
      <div className="navigation">
        <Link to={`/map`}>
          <div>
            <img src={map[0]} className="map" />
            <p className={map[1]}>
              <b></b>
            </p>
          </div>
        </Link>
        <Link to={`/list`}>
          <div>
            <img src={list[0]} className="list" />
            <p className={list[1]}>
              <b></b>
            </p>
          </div>
        </Link>
        <Link to={`/profile`}>
          <div>
            <img src={profile[0]} className="profile" />
            <p className={profile[1]}>
              <b></b>
            </p>
          </div>
        </Link>
      </div>
    </>
  );
}

export default Navigation;
