import "./App.css";
import "./index.css";
import { Route, Routes } from "react-router-dom";

import { createBrowserRouter, RouterProvider } from "react-router-dom";
import SignIn from "./SignIn.js";
import SignUp from "./SignUp.js";

import SendCode from "./SendCode.js";
import StationList from "./Stationlist.js";
import Sorting from "./ListSorting.js";
import Station from "./StationForMap.js";
import Filter from "./ListFilter.js";
import MapY from "./Map.js";
import List from "./List.js";
import Profile from "./Profile.js";
import DeleteAccount from "./DeleteAccount.js";
import PrivacyPolicy from "./PrivacyPolicy.js";
import Pay from "./Pay.js";
import UserEdit from "./UserEdit.js";
import PasswordChange from "./PasswordChange.js";
import Session from "./Session.js";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";

function App() {
  const router = createBrowserRouter([
    {
      path: "/signup",
      element: <SignUp />,
    },
    {
      path: "/",
      element: <SignIn />,
    },

    {
      path: "/sendcode",
      element: <SendCode />,
    },
    {
      path: "/map",
      element: <MapY />,
    },
    {
      path: "/map/:id",
      element: <MapY />,
    },
    {
      path: "/list",
      element: <List />,
    },
    {
      path: "/list/sorting",
      element: <Sorting />,
    },
    {
      path: "/list/filter",
      element: <Filter />,
    },
    {
      path: "/list/:params",
      element: <List />,
    },
    {
      path: "/list/filter",
      element: <Filter />,
    },
    {
      path: "/list/station/:id",
      element: <Station mode="forList" />,
    },
    {
      path: "/session/:idStation/:idGun",
      element: <Session />,
    },
    {
      path: "/session/:chargeOrderId",
      element: <Session />,
    },
    {
      path: "/profile",
      element: <Profile />,
    },
    {
      path: "/deleteaccount",
      element: <DeleteAccount />,
    },
    {
      path: "/privacypolicy",
      element: <PrivacyPolicy />,
    },
    {
      path: "/pay",
      element: <Pay />,
    },
    {
      path: "/useredit",
      element: <UserEdit />,
    },
    {
      path: "/passwordchange",
      element: <PasswordChange />,
    },
  ]);

  return <RouterProvider router={router} />;
}

export default App;
