import React, { useRef } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import backButton from "./backButton.png";
import stationInfo from "./stationInfo.png";
import markerDefault from "./marker.png";
import markerAlva from "./img/map/markerAlva.svg";
import markerAlvaAA from "./img/map/marker_alva_AA.svg";
import markerAlvaAU from "./img/map/marker_alva_AU.svg";
import markerAlvaUA from "./img/map/marker_alva_UA.svg";
import markerAlvaUU from "./img/map/marker_alva_UA.svg";
import markerOther from "./img/map/markerOther.svg";
import filterIcon from "./img/map/controls/filter.svg";
import locationIcon from "./img/map/controls/location.svg";
import zoomPlusIcon from "./img/map/controls/zoomPlus.svg";
import zoomMinusIcon from "./img/map/controls/zoomMinus.svg";
import { YMaps, Map, Placemark } from "@pbe/react-yandex-maps";
import { BottomSheet } from "react-spring-bottom-sheet";
import "react-spring-bottom-sheet/dist/style.css";
import { useState, useEffect } from "react";
import Station from "./station.js";
import Navigation from "./Navigation.js";
import MapFilter from "./MapFilter.js";
import StationForMap from "./StationForMap.js";
import "./index.css";

function MapY({ onChange }) {
  const { id } = useParams();
  const [open, setOpen] = useState(false);
  const [stations, setStations] = useState([]);
  const [stationsFiltered, setStationsFiltered] = useState([]);
  const [sheetContent, setSheetContent] = useState(["station", [450]]);
  const [currentStation, setCurrentStation] = useState(Number);
  const [location, setLocation] = useState({});
  const [coordinates, setCoordinates] = useState([]);
  const [center, setCenter] = useState([43.238, 76.8829]);
  const [zoom, setZoom] = useState(12);
  const [controlsVisibility, setControlsVisibility] = useState("controls");

  const mapState = React.useMemo(
    () => ({
      center,
      zoom,
    }),
    [zoom],
    [center]
  );

  function setParams({ onlyGBT, onlyCCS, onlyAvailable }) {
    setOpen(false);
    setControlsVisibility("controls");
    let filtered = [...stations];
    if (onlyGBT === false) {
      filtered = filtered.filter(
        (e) => e["guns"][0]["type"]["name"] === "GBT 22"
      );
    }
    if (onlyCCS === false) {
      filtered = filtered.filter(
        (e) => e["guns"][0]["type"]["name"] === "Type 2"
      );
    }
    if (onlyAvailable === true) {
      filtered = filtered.filter((e) => e.description.includes("ALVA"));
    }
    setStationsFiltered(filtered);
  }

  const navigate = useNavigate();

  function onDismiss() {
    setOpen(false);
    setControlsVisibility("controls");
  }

  async function stationGet() {
    let Token;

    let TokenR;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const stationList = await fetch("https://alvainfo.kz/api/station/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      let infoaboutstation = await stationList.json();

      if (infoaboutstation.server_error) {
        async function refresh() {
          const refreshResponse = await fetch(
            "https://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      console.log(infoaboutstation);
      setStations(infoaboutstation);
      setStationsFiltered(infoaboutstation);
      if (id) {
        setCurrentStation(id);
        let [latitude, longitude] = [
          await infoaboutstation[id - 1].latitude,
          await infoaboutstation[id - 1].longitude,
        ];
        setControlsVisibility("controls-none");
        setZoom(16);
        setCenter([latitude - 0.002, longitude + 0.00035]);
        setSheetContent(["station", 450]);
        setOpen(true);
      }
    } catch (e) {
      console.log("!");
    }
  }

  useEffect(() => {
    stationGet();
  }, []);

  function identityMarker(guns) {
    if (guns[0]?.status === "Available") {
      if (guns[1]?.status === "Available") {
        return markerAlvaAA;
      } else {
        return markerAlvaAU;
      }
    }
    if (guns[0]?.status === "Unavailable") {
      if (guns[1]?.status === "Unavailable") {
        return markerAlvaUU;
      } else {
        return markerAlvaUA;
      }
    }
    return markerAlva;
  }

  let MapObj = {};

  return (
    <div className="mapContainer">
      <YMaps>
        <Map
          state={mapState}
          width="100%"
          height="100%"
          className="mapAll"
          modules={["layout.ImageWithContent", "templateLayoutFactory"]}
          instanceRef={(object) => (MapObj = object)}
        >
          <div className={controlsVisibility}>
            <img
              src={filterIcon}
              onClick={() => {
                setControlsVisibility("controls-none");
                setOpen(true);
                setSheetContent(["filter", [410]]);
              }}
            />
            <img
              src={locationIcon}
              onClick={() => {
                navigator.geolocation.getCurrentPosition((position) => {
                  const { latitude, longitude } = position.coords;
                  MapObj.panTo([latitude, longitude]);
                });
              }}
            />
            <img
              src={zoomPlusIcon}
              onClick={() => {
                setCenter(MapObj.getCenter());
                setZoom((zoom) => (zoom += 1));
              }}
            />
            <img
              src={zoomMinusIcon}
              onClick={() => {
                setCenter(MapObj.getCenter());
                setZoom((zoom) => (zoom -= 1));
              }}
            />
          </div>
          {stationsFiltered.map((station) => {
            return (
              <Placemark
                geometry={[station.latitude, station.longitude]}
                options={{
                  iconLayout: "default#imageWithContent",
                  iconImageHref: `${
                    station.description.includes("ALVA") === true
                      ? identityMarker(station.guns)
                      : markerOther
                  }`,
                  iconImageOffset: [0, -50],
                  iconImageSize: [50, 60],
                  iconContentOffset: [7, -5],
                }}
                properties={{
                  iconContent: `<p class="placemarkVolume ${
                    station.description.includes("ALVA") === true &&
                    "placemarkAlva"
                  }">${station.total_charging_power}</p>`,
                }}
                onClick={() => {
                  setControlsVisibility("controls-none");
                  setCurrentStation(station.id);
                  console.log(
                    station.id,
                    currentStation,
                    station.latitude,
                    station.longitude
                  );
                  setCenter([
                    station.latitude - 0.002,
                    station.longitude + 0.00035,
                  ]);
                  MapObj.panTo([
                    station.latitude - 0.002,
                    station.longitude + 0.00035,
                  ]);

                  setZoom(16);
                  setSheetContent(["station", [450]]);
                  console.log(currentStation);
                  setOpen(true);
                }}
              />
            );
          })}
        </Map>
      </YMaps>

      <BottomSheet
        open={open}
        onDismiss={onDismiss}
        snapPoints={({ minHeight, maxHeight, footerHeight }) => [
          minHeight + 100,
          maxHeight,
        ]}
      >
        {sheetContent[0] === "filter" && <MapFilter handler={setParams} />}
        {sheetContent[0] !== "filter" && <StationForMap id={currentStation} />}
      </BottomSheet>

      <Navigation active="Map" />
    </div>
  );
}
export default MapY;
