import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import backButton from "./backButton.png";
import Navigation from "./Navigation.js";
import "./Session.css";

function Session({ id }) {
  const [stationID, setStationID] = useState(0);
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [totalPower, setTotalPower] = useState("");
  const [description, setDescription] = useState("");
  const [guns, setGuns] = useState([]);
  const [chargeOrder, setChargeOrder] = useState("");
  const [message, setMessage] = useState("");
  const [text, setText] = useState("");

  const [text2, setText2] = useState("");

  id = useParams().idStation;
  let idGun = useParams().idGun;

  console.log(" ", idGun);

  let chargeOrderID = useParams().chargeOrderId;

  if (chargeOrderID) {
    console.log("");
  }

  async function stationGet() {
    let Token;
    let TokenR;
    let idUser;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");

      idUser = parseJwt(localStorage.getItem("tokenAccess")).user_id;

      console.log(idUser);
    }

    function parseJwt(token) {
      let base64Url = token.split(".")[1];
      let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      let jsonPayload = decodeURIComponent(
        atob(base64)
          .split("")
          .map(function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join("")
      );
      return JSON.parse(jsonPayload);
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;

      idUser = parseJwt(tokenParse.access).user_id;

      console.log(idUser);
    }

    try {
      const stationList = await fetch(`https://alvainfo.kz/api/station/${id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      const remoteStart = await fetch(
        `https://alvainfo.kz/api/station/control/charge_order_remote_start/${idUser}/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: `Bearer ${Token}`,
          },
          body: JSON.stringify({
            gun_id: idGun,
          }),
        }
      );
      const responseRemoteStart = await remoteStart.json();

      if (responseRemoteStart) {
        setMessage("   (  , )");
        setChargeOrder(responseRemoteStart.id);

        let idObject = {
          chargeOrderID: responseRemoteStart.id,
        };

        if (localStorage) {
          console.log("  ");
          localStorage.setItem("sessionID", responseRemoteStart.id);
        }

        if (window.Mobile) {
          // eslint-disable-next-line no-undef
          window.Mobile.consumeSessionId(JSON.stringify(idObject));
        }
      } else {
        setMessage("-    ( )");
      }
      setText(JSON.stringify(responseRemoteStart));
      console.log(JSON.stringify(responseRemoteStart));
      console.log(chargeOrder);
      if (stationList.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "https://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      let infoaboutstation = await stationList.json();
      setStationID(infoaboutstation.id);
      setName(infoaboutstation.name);
      setAddress(infoaboutstation.address);
      setDescription(infoaboutstation.description);
      setGuns(infoaboutstation.guns);
    } catch (e) {
      console.log("!");
    }
  }

  async function stop() {
    let Token;
    let TokenR;
    let idUser;

    if (localStorage) {
      localStorage.setItem("sessionID", "null");
    }

    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      window.Mobile.consumeSessionId(JSON.stringify("null"));
    }

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");

      idUser = parseJwt(localStorage.getItem("tokenAccess")).user_id;

      console.log(idUser);
    }

    function parseJwt(token) {
      let base64Url = token.split(".")[1];
      let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      let jsonPayload = decodeURIComponent(
        atob(base64)
          .split("")
          .map(function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join("")
      );
      return JSON.parse(jsonPayload);
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;

      idUser = parseJwt(tokenParse.access).user_id;

      console.log(idUser);
    }

    try {
      const remoteStop = await fetch(
        `https://alvainfo.kz/api/station/control/charge_order_remote_stop/${idUser}/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: `Bearer ${Token}`,
          },
          body: JSON.stringify({
            charge_order_id: chargeOrder,
          }),
        }
      );
      const responseRemoteStop = await remoteStop.json();
      console.log(responseRemoteStop);
      setText2(JSON.stringify(responseRemoteStop));
      if (remoteStop.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "https://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stop();
      }
    } catch (e) {
      console.log("!");
    }
  }

  async function activeSessionGet() {
    let Token;
    let TokenR;
    let idUser;

    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");

      idUser = parseJwt(localStorage.getItem("tokenAccess")).user_id;

      console.log(idUser);
    }

    function parseJwt(token) {
      let base64Url = token.split(".")[1];
      let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      let jsonPayload = decodeURIComponent(
        atob(base64)
          .split("")
          .map(function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join("")
      );
      return JSON.parse(jsonPayload);
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;

      idUser = parseJwt(tokenParse.access).user_id;

      console.log(idUser);
    }

    try {
      const remoteStop = await fetch(
        `https://alvainfo.kz/api/station/control/charge_order/${chargeOrderID}`,
        {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: `Bearer ${Token}`,
          },
        }
      );
      const responseRemoteStop = await remoteStop.json();
      console.log(responseRemoteStop);
      setText(JSON.stringify(responseRemoteStop));
      if (responseRemoteStop.id) {
        setChargeOrder(responseRemoteStop.id);
      }
      if (remoteStop.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "https://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);
          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stop();
      }
    } catch (e) {
      console.log("!");
    }
  }

  useEffect(() => {
    if (!chargeOrderID) {
      stationGet();
    } else {
      activeSessionGet();
    }
  }, []);

  const navigate = useNavigate();

  return (
    <div className="session">
      <h1> </h1>
      <div className="buttons">
        <img src={backButton} onClick={() => navigate(-1)} />
      </div>
      {chargeOrderID ? <p className="stationName"> </p> : <></>}
      <p className="stationName">{name}</p>
      <p className="stationAddress">{address}</p>
      <p className="stationAddress">
           : {message}
      </p>

      <p className="stationAddress">JSON: {text}</p>
      <p className="stationAddress">{text2}</p>
      {description.length > 0 && (
        <p className="stationAddress">{description}</p>
      )}
      <div className="button" onClick={() => stop()}>
        <p> </p>
      </div>
      <Navigation active="Map" mode="Session" />
    </div>
  );
}

export default Session;
