import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import "./index.css";
import sortingButton from "./sortingButton.png";
import filterButton from "./filterButton.png";
import Navigation from "./Navigation.js";

function StationList() {
  const { sortParam } = useParams();

  const stations = [
    {
      id: 1,
      adress: ".   , . 1",
      rating: 100,
      price: 50,
      distance: 12,
    },
    {
      id: 2,
      adress: ".  , . 2",
      rating: 50,
      price: 40,
      distance: 5,
    },
    {
      id: 3,
      adress: ". , . 3",
      rating: 130,
      price: 80,
      distance: 4.2,
    },
    {
      id: 4,
      adress: ".  , . 4",
      rating: 200,
      price: 70,
      distance: 50.6,
    },
    {
      id: 5,
      adress: ". , . 5",
      rating: 5,
      price: 120,
      distance: 20,
    },
  ];

  console.log(sortParam);

  if (sortParam === "lowprice") {
    stations.sort(function (a, b) {
      if (a["price"] > b["price"]) {
        return 1;
      } else if (a["price"] === b["price"]) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  if (sortParam === "highrating") {
    stations.sort(function (a, b) {
      if (a["rating"] < b["rating"]) {
        return 1;
      } else if (a["rating"] === b["rating"]) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  if (sortParam === "near") {
    stations.sort(function (a, b) {
      if (a["distance"] > b["distance"]) {
        return 1;
      } else if (a["distance"] === b["distance"]) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  let filterMode = false;

  if (sortParam === "available") {
    filterMode = true;
  }

  return (
    <div>
      <h1></h1>
      <div className="buttons">
        <Link to={`/sorting`}>
          <img src={sortingButton} className="button" />
        </Link>
        <Link to={`/filter`}>
          <img src={filterButton} className="button" />
        </Link>
      </div>
      {filterMode &&
        stations
          .filter((i) => i["rating"] > 100)
          .map((i) => (
            <Link to={`/station/${i["id"]}`}>
              <div className="row">
                <p>{i.adress}</p>
                <p>{i.distance}   </p>
              </div>
            </Link>
          ))}

      {filterMode === false &&
        stations.map((i) => (
          <Link to={`/station/${i["id"]}`}>
            <div className="row">
              <p>{i.adress}</p>
              <p>{i.distance}   </p>
            </div>
          </Link>
        ))}

      <Navigation />
    </div>
  );
}

export default StationList;
