import Navigation from "./Navigation.js";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./index.css";
import "./List.css";
import sortingButton from "./sortingButton.png";
import filterButton from "./filterButton.png";
import ListFilter from "./ListFilter";
import ListSorting from "./ListSorting";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";

export default function List() {
  const { params } = useParams();
  const [stationlist, setStationlist] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [userLocation, setUserLocation] = useState({});
  let navigate = useNavigate();

  async function stationGet() {
    let Token;
    let TokenR;
    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;
    }

    try {
      const stationList = await fetch("https://alvainfo.kz/api/station/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
      });
      if (stationList.status === 401) {
        async function refresh() {
          const refreshResponse = await fetch(
            "https://alvainfo.kz/api/auth/token/refresh/",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
              body: JSON.stringify({
                refresh: TokenR,
              }),
            }
          );
          const data = refreshResponse.json();
          return data;
        }
        refresh().then((a) => {
          console.log(a);

          if (localStorage) {
            localStorage.setItem("tokenAccess", a.access);
            localStorage.setItem("tokenRefresh", a.refresh);
          }

          // eslint-disable-next-line no-undef
          if (window.Mobile) {
            // eslint-disable-next-line no-undef
            window.Mobile.consumeToken(JSON.stringify(a));
          }
        });
        return stationGet();
      }
      let infoaboutstation = await stationList.json();
      setStationlist(infoaboutstation);
      if (params === "available") {
        console.log(params);
        setStationlist(
          infoaboutstation.filter((station) =>
            station.description.includes("ALVA")
          )
        );
      }
    } catch (e) {
      console.log("!");
    }
  }

  useEffect(() => {
    stationGet();
    navigator.geolocation.getCurrentPosition((position) => {
      const { latitude, longitude } = position.coords;
      setUserLocation({ latitude, longitude });
    });
  }, []);

  if (params === "lowprice") {
    stationlist.sort(function (a, b) {
      if (a.guns[0]?.tariff?.cost < b.guns[0]?.tariff?.cost) {
        return 1;
      } else if (b.guns[0]?.tariff?.cost < a.guns[0]?.tariff?.cost) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  if (params === "highrating") {
    stationlist.sort(function (a, b) {
      if (a["rating"] < b["rating"]) {
        return 1;
      } else if (a["rating"] === b["rating"]) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  if (params === "powerfull") {
    stationlist.sort(function (a, b) {
      if (a["total_charging_power"] < b["total_charging_power"]) {
        return 1;
      } else if (a["total_charging_power"] === b["total_charging_power"]) {
        return 0;
      } else {
        return -1;
      }
    });
  }

  if (params === "near") {
    if (Object.entries(userLocation).length !== 0) {
      stationlist.sort(function (a, b) {
        if (
          haversineDistance(userLocation, {
            latitude: a.latitude,
            longitude: a.longitude,
          }) >
          haversineDistance(userLocation, {
            latitude: b.latitude,
            longitude: b.longitude,
          })
        ) {
          return 1;
        } else if (
          haversineDistance(userLocation, {
            latitude: a.latitude,
            longitude: a.longitude,
          }) ===
          haversineDistance(userLocation, {
            latitude: b.latitude,
            longitude: b.longitude,
          })
        ) {
          return 0;
        } else {
          return -1;
        }
      });
    }
  }

  let searchFilter = stationlist.filter((station) => {
    return (
      station.name.toLowerCase().includes(searchValue.toLowerCase()) ||
      station.address.toLowerCase().includes(searchValue.toLowerCase()) ||
      station.description.toLowerCase().includes(searchValue.toLowerCase()) ||
      String(station.total_charging_power)
        .toLowerCase()
        .includes(searchValue.toLowerCase()) ||
      station["guns"][0]["type"]["name"]
        .toLowerCase()
        .includes(searchValue.toLowerCase())
    );
  });

  if (searchFilter.length === 0) {
    searchFilter = [{ name: "  " }];
  }
  // hav(theta) = hav(bLat - aLat) + cos(aLat) * cos(bLat) * hav(bLon - aLon)
  function haversineDistance(a, b) {
    const asin = Math.asin;
    const cos = Math.cos;
    const sin = Math.sin;
    const sqrt = Math.sqrt;
    const PI = Math.PI;

    // equatorial mean radius of Earth (in meters)
    const R = 6378137;

    function squared(x) {
      return x * x;
    }
    function toRad(x) {
      return (x * PI) / 180.0;
    }
    function hav(x) {
      return squared(sin(x / 2));
    }
    const aLat = toRad(Array.isArray(a) ? a[1] : a.latitude ?? a.lat);
    const bLat = toRad(Array.isArray(b) ? b[1] : b.latitude ?? b.lat);
    const aLng = toRad(Array.isArray(a) ? a[0] : a.longitude ?? a.lng ?? a.lon);
    const bLng = toRad(Array.isArray(b) ? b[0] : b.longitude ?? b.lng ?? b.lon);
    const ht = hav(bLat - aLat) + cos(aLat) * cos(bLat) * hav(bLng - aLng);
    return 2 * R * asin(sqrt(ht));
  }

  return (
    <div className="list">
      <header>
        <h1></h1>
        <div>
          <form className="search">
            <input
              type="text"
              placeholder=""
              onChange={(event) => setSearchValue(event.target.value)}
            />
          </form>
          <Link to={`/list/sorting`}>
            <img src={sortingButton} className="button" />
          </Link>
          <Link to={`/list/filter`}>
            <img src={filterButton} className="button" />
          </Link>
        </div>
      </header>
      {searchFilter.map((e, i) => {
        console.log(e);
        return (
          <Link to={`/list/station/${e.id}`}>
            <div className="preview">
              <p>{e.name}</p>
              {Object.entries(userLocation).length !== 0 && e.latitude && (
                <p>
                  {(
                    haversineDistance(userLocation, {
                      latitude: e.latitude,
                      longitude: e.longitude,
                    }) / 1000
                  ).toFixed(2)}
                  &nbsp;  
                </p>
              )}
            </div>
          </Link>
        );
      })}
      <Navigation active="List" />
    </div>
  );
}
