import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./signIn+signUp.css";
import submitIcon from "./img/submit.svg";
import alvaLogo from "./img/alva_logo.svg";

export default function SignUp() {
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [authMistake, setAuthMistake] = useState(false);
  const [badRequest, setBadRequest] = useState("");

  let navigate = useNavigate();

  function onSubmitHandler(e) {
    e.preventDefault();

    signUp(login, password, passwordConfirm).then((isSucces) => {
      if (isSucces) {
        signIn(login, password)
          .then((tokensObject) => {
            console.log(tokensObject);

            if (localStorage) {
              localStorage.clear();
              localStorage.setItem("tokenAccess", tokensObject.access);
              localStorage.setItem("tokenRefresh", tokensObject.refresh);
            }

            // eslint-disable-next-line no-undef
            if (window.Mobile) {
              // eslint-disable-next-line no-undef
              window.Mobile.consumeToken(JSON.stringify(tokensObject));
            }

            if (window["webkit"]) {
              if (window["webkit"].messageHandlers) {
                if (window["webkit"].messageHandlers.callbackHandler) {
                  window["webkit"].messageHandlers.callbackHandler.postMessage(
                    JSON.stringify(tokensObject)
                  );
                }
              }
            }

            navigate("/map");
          })
          .catch((error) => console.log(error));
      }
    });
  }

  async function signUp(phoneNumber, password, password2) {
    let authType = "phone_number";
    if (phoneNumber.includes("@")) {
      authType = "email";
    }
    try {
      const response = await fetch("https://alvainfo.kz/api/auth/register/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          phone_number: phoneNumber,
          password: password,
          password2: password2,
          auth_type: "phone_number",
        }),
      });

      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log(mistake);
        if (mistake.password) {
          setBadRequest("  .");
        }
        setBadRequest(mistake.phone_number[0]);
        setAuthMistake(true);
        throw new Error("401 or badrequest");
      } else {
        setAuthMistake(false);
      }

      const aboutUser = await response.json();

      if (aboutUser.server_error) {
        setAuthMistake(true);
        setBadRequest("  ");
        throw new Error("401 or badrequest");
      }

      if (localStorage) {
        localStorage.setItem("idUser", aboutUser.id);
      }

      return response.ok;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  async function signIn(phoneNumber, password) {
    try {
      const signInResponse = await fetch(
        "https://alvainfo.kz/api/auth/token/",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify({
            phone_number: phoneNumber,
            password: password,
            auth_type: "phone_number",
          }),
        }
      );
      if (signInResponse.status === 401 || !signInResponse.ok) {
        setAuthMistake(true);
        throw new Error("401 or badrequest");
      } else {
        setAuthMistake(false);
      }
      const data = await signInResponse.json();
      return data;
    } catch (error) {
      console.log("eee", error);
      return null;
    }
  }

  let inputClass = authMistake ? "mistake" : "default";

  return (
    <div className="signUp">
      <header>
        <Link to={`/`}>
          <p></p>
        </Link>
        <h1></h1>
        <div></div>
      </header>
      <form onSubmit={onSubmitHandler}>
        <label>
          <input
            className={inputClass}
            type="text"
            placeholder="   "
            onChange={(event) => setLogin(event.target.value)}
          />
          <input
            className={inputClass}
            type="password"
            placeholder=""
            onChange={(event) => setPassword(event.target.value)}
          />

          <input
            className={inputClass}
            type="password"
            placeholder=" "
            onChange={(event) => setPasswordConfirm(event.target.value)}
          />
        </label>

        <p className="badrequest">{badRequest}</p>

        <div className="submit">
          <input type="image" src={submitIcon} />
        </div>
      </form>
    </div>
  );
}
