import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./SendCode.css";
import submitIcon from "./img/submit.svg";
import alvaLogo from "./img/alva_logo.svg";

export default function SendCode() {
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [authMistake, setAuthMistake] = useState(false);
  const [badRequest, setBadRequest] = useState("");
  const [code, setCode] = useState("");

  let navigate = useNavigate();

  async function getCode() {
    let Token;

    let TokenR;

    let idUser;
    if (localStorage) {
      Token = localStorage.getItem("tokenAccess");
      TokenR = localStorage.getItem("tokenRefresh");
      idUser = parseJwt(localStorage.getItem("tokenAccess")).user_id;

      console.log(idUser);
      console.log(parseJwt(localStorage.getItem("tokenAccess")));
    }

    function parseJwt(token) {
      let base64Url = token.split(".")[1];
      let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      let jsonPayload = decodeURIComponent(
        atob(base64)
          .split("")
          .map(function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join("")
      );
      return JSON.parse(jsonPayload);
    }

    let tokens = "";

    // eslint-disable-next-line no-undef
    if (window.Mobile) {
      // eslint-disable-next-line no-undef
      tokens = window.Mobile.getToken();
      let tokenParse = JSON.parse(tokens);
      Token = tokenParse.access;
      TokenR = tokenParse.refresh;

      idUser = parseJwt(tokenParse.access).user_id;

      console.log(idUser);
    }

    try {
      const response = await fetch(
        `https://alvainfo.kz/api/auth/send_confirmation_code/${idUser}/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: `Bearer ${Token}`,
          },
        }
      );
      console.log(response);
      let response2 = response.json();
      console.log(response2);
      if (response.status === 401 || !response.ok) {
        const mistake = await response.json();
        console.log(mistake);
        throw new Error("401 or badrequest");
      } else {
      }
      return response.ok;
    } catch (error) {
      console.log(error);
      return null;
    }
  }

  function onSubmitHandler(e) {
    e.preventDefault();
    console.log(code);

    sendCode(code);

    async function sendCode(code) {
      let Token;

      let TokenR;

      let idUser;
      if (localStorage) {
        Token = localStorage.getItem("tokenAccess");
        TokenR = localStorage.getItem("tokenRefresh");
        idUser = parseJwt(localStorage.getItem("tokenAccess")).user_id;

        console.log(idUser);
        console.log(parseJwt(localStorage.getItem("tokenAccess")));
      }

      function parseJwt(token) {
        let base64Url = token.split(".")[1];
        let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
        let jsonPayload = decodeURIComponent(
          atob(base64)
            .split("")
            .map(function (c) {
              return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
            })
            .join("")
        );
        return JSON.parse(jsonPayload);
      }

      let tokens = "";

      // eslint-disable-next-line no-undef
      if (window.Mobile) {
        // eslint-disable-next-line no-undef
        tokens = window.Mobile.getToken();
        let tokenParse = JSON.parse(tokens);
        Token = tokenParse.access;
        TokenR = tokenParse.refresh;

        idUser = parseJwt(tokenParse.access).user_id;

        console.log(idUser);
      }

      try {
        const signInResponse = await fetch(
          `https://alvainfo.kz/api/auth/use_confirmation_code/${idUser}/`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Accept: "application/json",
              Authorization: `Bearer ${Token}`,
            },
            body: JSON.stringify({
              code: code,
            }),
          }
        );

        const data = await signInResponse.json();
        console.log(data);
        if (data.server_error) {
          setBadRequest("  ");
          setAuthMistake(true);
          throw new Error("Error");
        } else {
          navigate("/map");
        }
        if (signInResponse.status === 401 || !signInResponse.ok) {
          console.log("401 or badrequest");
          setBadRequest(data.detail);
          if (data.detail === undefined) {
            setBadRequest("  ");
          }
          setAuthMistake(true);
          throw new Error("401 or badrequest");
        } else {
          setAuthMistake(false);
        }
        console.log("hi", data);
        return data;
      } catch (error) {
        console.log(error);
        return;
      }
    }
  }

  useEffect(() => {
    getCode();
  }, []);

  let inputClass = authMistake ? "mistake" : "default";

  return (
    <div className="signUp">
      <header>
        <Link to={`/`}>
          <p></p>
        </Link>
        <h1> </h1>
        <div></div>
      </header>
      <form onSubmit={onSubmitHandler}>
        <p> ,     </p>
        <label>
          <input
            className={inputClass}
            type="text"
            placeholder=""
            onChange={(event) => setCode(event.target.value)}
          />
        </label>

        <p className="badrequest">{badRequest}</p>

        <div className="submit">
          <input type="image" src={submitIcon} />
        </div>
      </form>
    </div>
  );
}
